"use client"

import { useState, useEffect, useRef } from "react"
import Link from "next/link"
import { DashboardShell } from "@/components/dashboard-shell"
import { Button } from "@/components/ui/button"
import { Textarea } from "@/components/ui/textarea"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogTrigger } from "@/components/ui/dialog"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { Badge } from "@/components/ui/badge"
import { Progress } from "@/components/ui/progress"
import { 
  Network, 
  Brain, 
  Zap, 
  Activity, 
  ArrowLeft,
  Settings,
  Play,
  Pause,
  CheckCircle,
  AlertCircle,
  Clock,
  Loader2,
  Sparkles,
  GitBranch,
  Eye,
  EyeOff
} from "lucide-react"
import { generateNetworkFromText } from "@/actions/generate-network"
import { MODEL_CONFIG, PRESET_MODELS } from "@/lib/model-config"

interface ProcessingStep {
  id: string
  title: string
  description: string
  status: "pending" | "processing" | "completed" | "error"
  progress?: number
}

interface NetworkNode {
  id: string
  label: string
  type: "concept" | "entity" | "method"
  x: number
  y: number
  vx?: number
  vy?: number
  fx?: number | null
  fy?: number | null
  color?: string
  radius?: number
}

interface NetworkLink {
  id: string
  source: string
  target: string
  label?: string
  type: string
}

interface NetworkData {
  nodes: NetworkNode[]
  links: NetworkLink[]
  metadata?: {
    title?: string
    description?: string
    processingTime?: number
    nodeCount?: number
    linkCount?: number
  }
}

export default function KnowledgeNetworkPage() {
  const [inputText, setInputText] = useState("")
  const [selectedModel, setSelectedModel] = useState(PRESET_MODELS[0].id)
  const [isProcessing, setIsProcessing] = useState(false)
  const [networkData, setNetworkData] = useState<NetworkData | null>(null)
  const [processingSteps, setProcessingSteps] = useState<ProcessingStep[]>([])
  const [showVisualization, setShowVisualization] = useState(false)
  const [currentStep, setCurrentStep] = useState(0)
  const [showConfig, setShowConfig] = useState(false)
  const [progress, setProgress] = useState(0)

  const addProcessingStep = (step: ProcessingStep) => {
    setProcessingSteps(prev => [...prev, step])
  }

  const updateProcessingStep = (id: string, updates: Partial<ProcessingStep>) => {
    setProcessingSteps(prev => prev.map(step => 
      step.id === id ? { ...step, ...updates } : step
    ))
  }

  const handleGenerateNetwork = async () => {
    if (!inputText.trim()) return
    
    setIsProcessing(true)
    setNetworkData(null)
    setShowVisualization(false)
    setProgress(0)
    setCurrentStep(0)
    
    // Step 1: Initialize
    addProcessingStep({
      id: "init",
      title: "Initializing Network Engine",
      description: "Setting up environment and preparing for knowledge extraction",
      status: "pending"
    })
    updateProcessingStep("init", { status: "processing" })
    
    try {
      const modelConfig = PRESET_MODELS.find(m => m.id === selectedModel)
      if (!modelConfig) {
        throw new Error("Model configuration not found")
      }

      addProcessingStep({
        id: "query",
        title: "Querying Model",
        description: `Sending request to ${modelConfig.name} for network generation`,
        status: "pending"
      })
      updateProcessingStep("query", { status: "processing" })

      // Step 2: Query model for network generation
      const result = await generateNetworkFromText(inputText, {
        apiKey: modelConfig.apiKey,
        baseURL: modelConfig.baseURL,
        model: modelConfig.model
      })

      updateProcessingStep("query", { status: "completed", progress: 100 })

      // Step 3: Process response
      addProcessingStep({
        id: "process",
        title: "Processing Response",
        description: "Analyzing model output and extracting network structure",
        status: "pending"
      })
      updateProcessingStep("process", { status: "processing" })

      let processedData: NetworkData
      
      if (result.nodes && result.links) {
        processedData = {
          nodes: result.nodes.map((node: any, index: number) => ({
            ...node,
            id: node.id || `node-${index}`,
            x: Math.random() * 800 + 100,
            y: Math.random() * 600 + 100,
            vx: 0,
            vy: 0,
            color: node.type === "concept" ? "#3b82f6" : 
                   node.type === "entity" ? "#10b981" : 
                   node.type === "method" ? "#8b5cf6" : "#6366f1",
            radius: node.type === "concept" ? 35 : node.type === "entity" ? 25 : 20
          })),
          links: result.links.map((link: any) => ({
            ...link,
            id: `link-${link.source}-${link.target}`
          })),
          metadata: {
            title: `${inputText.substring(0, 50)}${inputText.length > 50 ? "..." : ""} Knowledge Network`,
            description: `Generated network with ${result.nodes.length} nodes and ${result.links.length} connections`,
            processingTime: Date.now() - Date.now() + 3000
          }
        }
        
        updateProcessingStep("process", { status: "completed", progress: 100 })

        // Step 4: Finalize
        addProcessingStep({
          id: "finalize",
          title: "Finalizing Network",
          description: "Preparing visualization and completing the process",
          status: "pending"
        })
        updateProcessingStep("finalize", { status: "processing" })

        setNetworkData(processedData)
        setShowVisualization(true)
        setCurrentStep(4)
        
        // Simulate final processing
        setTimeout(() => {
          updateProcessingStep("finalize", { 
            status: "completed",
            progress: 100
          })
          setIsProcessing(false)
          setProgress(100)
        }, 1500)
      }

    } catch (error) {
      console.error("Network generation failed:", error)
      
      addProcessingStep({
        id: "error",
        title: "Generation Failed",
        description: `Error: ${error instanceof Error ? error.message : "Unknown error"}`,
        status: "error"
      })
      
      updateProcessingStep("error", { status: "completed" })
      setIsProcessing(false)
    }
  }

  const handleReset = () => {
    setInputText("")
    setNetworkData(null)
    setProcessingSteps([])
    setShowVisualization(false)
    setCurrentStep(0)
    setProgress(0)
  }

  const getStepIcon = (status: ProcessingStep["status"]) => {
    switch (status) {
      case "pending":
        return <Clock className="w-4 h-4 text-gray-400" />
      case "processing":
        return <Loader2 className="w-4 h-4 text-blue-500 animate-spin" />
      case "completed":
        return <CheckCircle className="w-4 h-4 text-green-500" />
      case "error":
        return <AlertCircle className="w-4 h-4 text-red-500" />
      default:
        return <Clock className="w-4 h-4 text-gray-400" />
    }
  }

  const getStepColor = (status: ProcessingStep["status"]) => {
    switch (status) {
      case "pending":
        return "bg-gray-100"
      case "processing":
        return "bg-blue-100"
      case "completed":
        return "bg-green-100"
      case "error":
        return "bg-red-100"
      default:
        return "bg-gray-100"
    }
  }

  return (
    <DashboardShell>
      <div className="flex flex-col h-screen bg-background">
        {/* Header */}
        <header className="flex-none h-16 border-b border-slate-200 bg-white px-6 flex items-center justify-between shrink-0">
          <div className="flex items-center gap-4">
            <Link href="/">
              <Button variant="ghost" size="icon" className="hover:bg-slate-100 text-slate-600">
                <ArrowLeft className="h-5 w-5" />
              </Button>
            </Link>
            <h1 className="text-3xl font-bold tracking-tight flex items-center gap-2">
              <Network className="h-8 w-8 text-primary" />
              Network Engine
              <Badge className="ml-2 bg-primary/10 text-primary px-2 py-1 rounded-full">
                AI-Powered
              </Badge>
            </h1>
          </div>
          <div className="flex items-center gap-3">
            <div className="flex items-center gap-2 bg-slate-100 rounded-lg p-1">
              <span className="text-xs font-medium text-slate-500 px-2">Model:</span>
              <Select value={selectedModel} onValueChange={setSelectedModel} disabled={isProcessing}>
                <SelectTrigger className="h-7 w-[200px] text-xs bg-white border-slate-200 text-slate-700 focus:ring-primary/50 shadow-sm">
                  <SelectValue />
                </SelectTrigger>
                <SelectContent className="bg-white border-slate-200 text-slate-700">
                  {PRESET_MODELS.map((model) => (
                    <SelectItem key={model.id} value={model.id} className="text-xs focus:bg-slate-100 focus:text-primary-600">
                      {model.name}
                    </SelectItem>
                  ))}
                </SelectContent>
              </Select>
            </div>
            
            <Dialog>
              <DialogTrigger asChild>
                <Button variant="outline" size="icon" disabled={isProcessing}>
                  <Settings className="h-4 w-4" />
                </Button>
              </DialogTrigger>
              <DialogContent className="bg-slate-950/95 backdrop-blur-sm sm:rounded-2xl">
                <DialogHeader>
                  <DialogTitle>Model Configuration</DialogTitle>
                </DialogHeader>
                <div className="space-y-4">
                  <p className="text-sm text-slate-600">
                    Configure AI model parameters for network generation:
                  </p>
                  <div className="space-y-2">
                    <div className="flex items-center justify-between">
                      <span className="text-xs font-medium text-slate-500">Temperature</span>
                      <span className="text-xs text-slate-400">0.7</span>
                    </div>
                    <div className="flex items-center justify-between">
                      <span className="text-xs font-medium text-slate-500">Max Tokens</span>
                      <span className="text-xs text-slate-400">4096</span>
                    </div>
                  </div>
                  <div className="pt-4 space-y-2">
                    <div className="flex items-center justify-between">
                      <span className="text-xs font-medium text-slate-500">Entity Extraction</span>
                      <div className="flex items-center gap-2">
                        <input type="checkbox" defaultChecked className="rounded" readOnly />
                      </div>
                    </div>
                    <div className="flex items-center justify-between">
                      <span className="text-xs font-medium text-slate-500">Relation Mining</span>
                      <div className="flex items-center gap-2">
                        <input type="checkbox" defaultChecked className="rounded" readOnly />
                      </div>
                    </div>
                    <div className="flex items-center justify-between">
                      <span className="text-xs font-medium text-slate-500">Causal Inference</span>
                      <div className="flex items-center gap-2">
                        <input type="checkbox" defaultChecked className="rounded" readOnly />
                      </div>
                    </div>
                  </div>
                  <div className="pt-4 space-y-2">
                    <div className="flex items-center justify-between">
                      <span className="text-xs font-medium text-slate-500">API Key Override</span>
                      <input type="password" placeholder="Use system default" className="rounded bg-slate-800 text-slate-200 px-2 py-1" />
                    </div>
                  </div>
                </div>
              </DialogContent>
            </Dialog>
          </div>
        </header>

        {/* Main Content */}
        <main className="flex-1 overflow-hidden flex">
          <div className="w-[380px] flex-shrink-0 bg-slate-50 border-r border-slate-200">
            <div className="p-4 space-y-4">
              {/* Input Section */}
              <div className="space-y-2">
                <h2 className="text-lg font-semibold mb-3">Network Query</h2>
                <p className="text-sm text-slate-600 mb-4">
                  Enter your research topic or question to generate a causal knowledge network
                </p>
                <Textarea
                  value={inputText}
                  onChange={(e) => setInputText(e.target.value)}
                  placeholder="e.g., 'What are the causal relationships between machine learning and model performance?'"
                  className="min-h-[120px] bg-white border-slate-200 rounded-lg p-3 text-sm resize-none focus:ring-2 focus:ring-primary/500 focus:border-transparent"
                  disabled={isProcessing}
                />
                
                <div className="flex gap-2">
                  <Button
                    onClick={handleGenerateNetwork}
                    disabled={isProcessing || !inputText.trim()}
                    className="flex-1 bg-primary text-white hover:bg-primary/90 shadow-lg"
                    size="lg"
                  >
                    {isProcessing ? (
                      <>
                        <Loader2 className="w-4 h-4 mr-2 animate-spin" />
                        Generating...
                      </>
                    ) : (
                      <>
                        <Sparkles className="w-4 h-4 mr-2" />
                        Generate Network
                      </>
                    )}
                  </Button>
                  
                  <Button
                    variant="outline"
                    onClick={handleReset}
                    disabled={isProcessing}
                  >
                    Reset
                  </Button>
                </div>
              </div>

              {/* Processing Steps */}
              {processingSteps.length > 0 && (
                <div className="mt-6">
                  <h3 className="text-lg font-semibold mb-3">Processing Steps</h3>
                  <div className="space-y-2">
                    {processingSteps.map((step) => (
                      <div key={step.id} className={`p-3 rounded-lg border ${getStepColor(step.status)} transition-all`}>
                        <div className="flex items-center gap-3">
                          {getStepIcon(step.status)}
                          <div className="flex-1">
                            <div className="font-medium">{step.title}</div>
                            <div className="text-sm text-slate-600">{step.description}</div>
                          </div>
                        </div>
                        {step.progress !== undefined && (
                          <div className="mt-2">
                            <div className="flex justify-between text-xs text-slate-500 mb-1">
                              <span>Progress</span>
                              <span className="text-slate-700 font-medium">{step.progress}%</span>
                            </div>
                            <Progress value={step.progress} className="h-2" />
                          </div>
                        )}
                      </div>
                    ))}
                  </div>
                </div>
              )}

              {/* Progress Overview */}
              {isProcessing && (
                <div className="mt-6">
                  <h3 className="text-lg font-semibold mb-3">Overall Progress</h3>
                  <div className="space-y-2">
                    <div className="flex justify-between text-sm">
                      <span>Generation Progress</span>
                      <span className="text-primary font-medium">{progress}%</span>
                    </div>
                    <Progress value={progress} className="h-3" />
                  </div>
                </div>
              )}
            </div>
          </div>

          {/* Right Panel - Visualization */}
          <div className="flex-1 bg-black">
            {networkData && (
              <div className="h-full flex flex-col">
                {/* Network Header */}
                <div className="bg-slate-900/10 backdrop-blur-sm border-b border-slate-800 p-4">
                  <div className="flex items-center justify-between">
                    <div>
                      <h2 className="text-xl font-bold text-white flex items-center gap-2">
                        <Network className="h-6 w-6 text-blue-400" />
                        {networkData.metadata?.title || "Knowledge Network"}
                      </h2>
                      {networkData.metadata?.description && (
                        <p className="text-slate-400 text-sm mt-1">
                          {networkData.metadata.description}
                        </p>
                      )}
                    </div>
                    <div className="flex items-center gap-2">
                      <Badge variant="secondary" className="bg-slate-800">
                        {networkData.nodes.length} Nodes
                      </Badge>
                      <Badge variant="secondary" className="bg-slate-800">
                        {networkData.links.length} Links
                      </Badge>
                      <div className="flex gap-2">
                        <Button
                          variant="ghost"
                          size="icon"
                          className="text-slate-400 hover:text-white hover:bg-slate-700"
                          onClick={() => setShowVisualization(!showVisualization)}
                        >
                          {showVisualization ? <EyeOff className="h-4 w-4" /> : <Eye className="h-4 w-4" />}
                        </Button>
                        <Button
                          variant="ghost"
                          size="icon"
                          className="text-slate-400 hover:text-white hover:bg-slate-700"
                        >
                          <GitBranch className="h-4 w-4" />
                        </Button>
                        <Button
                          variant="ghost"
                          size="icon"
                          className="text-slate-400 hover:text-white hover:bg-slate-700"
                        >
                          <Sparkles className="h-4 w-4" />
                        </Button>
                      </div>
                    </div>
                  </div>
                </div>

                {/* Network Visualization */}
                <NetworkVisualization 
                  data={networkData}
                  showLabels={showVisualization}
                  onNodeClick={(node) => {
                    console.log("Node clicked:", node)
                  }}
                  onLinkClick={(link) => {
                    console.log("Link clicked:", link)
                  }}
                />
              </div>
            )}
          </div>
        </main>
      </div>
    </DashboardShell>
  )
}

// Network Visualization Component
interface NetworkVisualizationProps {
  data: NetworkData
  showLabels: boolean
  onNodeClick?: (node: NetworkNode) => void
  onLinkClick?: (link: NetworkLink) => void
}

function NetworkVisualization({ data, showLabels, onNodeClick, onLinkClick }: NetworkVisualizationProps) {
  const canvasRef = useRef<HTMLCanvasElement>(null)
  const animationRef = useRef<number | null>(null)
  const particlesRef = useRef<any[]>([])

  useEffect(() => {
    const canvas = canvasRef.current
    if (!canvas || !data) return

    const ctx = canvas.getContext("2d")
    if (!ctx) return

    // Set canvas size
    const resizeCanvas = () => {
      const container = canvas.parentElement
      if (container) {
        canvas.width = container.clientWidth
        canvas.height = container.clientHeight
      }
    }

    resizeCanvas()
    window.addEventListener("resize", resizeCanvas)

    // Animation loop
    let time = 0
    const animate = () => {
      time += 0.01
      
      // Clear canvas
      ctx.fillStyle = "#0a0a0a"
      ctx.fillRect(0, 0, canvas.width, canvas.height)

      // Update node positions with physics
      data.nodes.forEach((node, i) => {
        if (node.fx !== undefined && node.fx !== null && node.fy !== undefined && node.fy !== null) {
          node.x += (node.fx - node.x) * 0.05
          node.y += (node.fy - node.y) * 0.05
        }
        
        // Add some organic movement
        node.x += Math.sin(time + i) * 0.5
        node.y += Math.cos(time + i) * 0.3
      })

      // Draw links
      data.links.forEach((link) => {
        const sourceNode = data.nodes.find((n) => n.id === link.source)
        const targetNode = data.nodes.find((n) => n.id === link.target)
        
        if (sourceNode && targetNode) {
          ctx.beginPath()
          ctx.moveTo(sourceNode.x, sourceNode.y)
          
          // Calculate control points for curved links
          const dx = targetNode.x - sourceNode.x
          const dy = targetNode.y - sourceNode.y
          const cx = sourceNode.x + dx / 2
          const cy = sourceNode.y + dy / 2
          
          // Draw curved line
          ctx.quadraticCurveTo(cx, cy, targetNode.x, targetNode.y)
          
          ctx.strokeStyle = "rgba(148, 163, 184, 0.3)"
          ctx.lineWidth = 2
          ctx.stroke()
        }
      })

      // Draw nodes
      data.nodes.forEach((node, i) => {
        // Node shadow
        ctx.shadowColor = node.color ? `${node.color}40` : "rgba(0, 0, 0, 0.2)"
        ctx.shadowBlur = 10
        
        // Draw node
        ctx.beginPath()
        ctx.arc(node.x, node.y, node.radius || 25, 0, 2 * Math.PI)
        
        if (node.color) {
          ctx.fillStyle = node.color
        } else {
          const gradient = ctx.createRadialGradient(
            node.x, node.y, 0,
            node.x, node.y, node.radius || 25
          )
          gradient.addColorStop(0, node.color || "#6366f1")
          gradient.addColorStop(1, `${node.color || "#6366f1"}80`)
          ctx.fillStyle = gradient
        }
        
        ctx.fill()
        
        // Reset shadow for next node
        ctx.shadowColor = "transparent"
        ctx.shadowBlur = 0

        // Draw labels if enabled
        if (showLabels && node.label) {
          ctx.shadowColor = "rgba(0, 0, 0, 0.5)"
          ctx.shadowBlur = 3
          ctx.fillStyle = "#ffffff"
          ctx.font = "12px Inter"
          ctx.textAlign = "center"
          ctx.textBaseline = "middle"
          
          // Background for text
          const textMetrics = ctx.measureText(node.label)
          const padding = 6
          const bgX = node.x - textMetrics.width / 2 - padding
          const bgY = node.y + (node.radius || 25) + 8
          const bgWidth = textMetrics.width + padding * 2
          const bgHeight = 20
          
          ctx.fillStyle = "rgba(0, 0, 0, 0.8)"
          ctx.fillRect(bgX - 2, bgY - 10, bgWidth, bgHeight)
          
          ctx.fillStyle = "#ffffff"
          ctx.fillText(node.label, node.x, node.y + (node.radius || 25))
        }
      })

      // Draw particles
      particlesRef.current.forEach((particle, index) => {
        particle.progress += 0.02
        if (particle.progress >= 1) {
          particlesRef.current.splice(index, 1)
          return
        }
        
        const currentX = particle.x + (particle.tx - particle.x) * particle.progress
        const currentY = particle.y + (particle.ty - particle.y) * particle.progress
        
        ctx.beginPath()
        ctx.arc(currentX, currentY, 1.5, 0, 2 * Math.PI)
        ctx.fillStyle = "rgba(255, 255, 255, 0.6)"
        ctx.fill()
      })

      animationRef.current = requestAnimationFrame(animate)
    }

    animate()

    return () => {
      window.removeEventListener("resize", resizeCanvas)
      if (animationRef.current) {
        cancelAnimationFrame(animationRef.current)
      }
    }
  }, [data, showLabels])

  return (
    <canvas 
      ref={canvasRef}
      className="w-full h-full cursor-move"
      onClick={(e) => {
        const rect = canvasRef.current?.getBoundingClientRect()
        if (rect) {
          const x = e.clientX - rect.left
          const y = e.clientY - rect.top
          
          // Find clicked node
          const clickedNode = data.nodes.find((node) => {
            const dx = x - node.x
            const dy = y - node.y
            const distance = Math.sqrt(dx * dx + dy * dy)
            return distance < (node.radius || 25)
          })
          
          if (clickedNode && onNodeClick) {
            onNodeClick(clickedNode)
          }
        }
      }}
    />
  )
}
